import React from 'react';
import s from './ProfileInfo.module.css'
import ava from '../../../assets/images/content-profile-ico.png'
import notimg from '../../../assets/images/notPhotoUsers.png'
import Preloader from '../../common/preloader/preoader';
import job from '../../../assets/images/calc.png'
import notJob from '../../../assets/images/no_djob.png'
import ProfileStatusWithHooks from './profilestatus/profileStatusWithHooks';

const ProfileInfo = ({profile, status, updateUserStatus}) => {
  if(!profile){
    return <Preloader profile={true}/>
  }
    return (
      <div className={s.avatar}>
        <div className={s.photo}>
          {profile.userId === 6096 ? <img alt="" src={ava}/>:<img src={profile.photos.small ? profile.photos.small: notimg} alt=""/>}
        </div>
        <div className={s.descr}>
          <div className={s.name}>{profile.fullName}</div>
          <div className={s.text}>
            <ProfileStatusWithHooks status={status} updateUserStatus={updateUserStatus}/>
          </div>
          <div className={s.text}>Статус: {profile.aboutMe}</div>
          <div className={s.text}>Поиск работы: {profile.lookingForAJobDescription}</div>
    <div className={s.text}>{profile.lookingForAJob? <div className={s.job}>Работаю:<img alt="" src={job}/></div>:<div className={s.not_job}>Не работаю:<img alt="" src={notJob}/></div>}</div>
        </div>
      </div>
  )
}

export default ProfileInfo;