import React from 'react';
import s from './myPosts.module.css';
import Post from './post/Post';
import { reduxForm } from 'redux-form';
import {required, maxLengthCreator} from '../../../utls/validators/validators'
import { Textarea, createField } from '../../common/FormControls/FormsControls';

const maxLength10 = maxLengthCreator(30);

const MyPosts = React.memo(props => {

  let postsElements = props.postsData.map( p => <Post message={p.message} like={p.like} key={p.id}/>)

  
  let onAddPost = (value) => {
    props.addPost(value.newPost);
  }


  return (
      <div className={s.post}>
        <div className={s.area}>
          <div className={s.title}>
            Новый пост
          </div>
          <NewPostReduxForm onSubmit={onAddPost}/>
        </div>
        {postsElements}
      </div>
  )
});

const PostForm = (props) => {
  return(
    <form onSubmit={props.handleSubmit}>
      {createField("Введите сообщение", "newPost", [required, maxLength10], Textarea)}
      <button>Отправить</button>
    </form>
  )
}

const NewPostReduxForm = reduxForm ({
  form: 'newPost' 
})(PostForm)

export default MyPosts;