import React from 'react';
import style from './profileStatus.module.css';
import { useState } from 'react';
import { useEffect } from 'react';

const ProfileStatusWithHooks = (props) => {

  let [editMode, setEditMode] = useState(false);
  let [status, setStatus] = useState(props.status);

  useEffect( () => {
      setStatus(props.status)
  }, [props.status] );

  const activateEditMode = () => {
      setEditMode(true)
  }

  const deactivateEditMode = () => {
    setEditMode(false)
    if(status !== props.status){
      props.updateUserStatus(status)
    }
  }

  const keyUpUse = (e) => {
    if(e.keyCode === 13){
      deactivateEditMode()
    }
  }

  const onStatusChange = (e) => {
      setStatus(e.currentTarget.value)
  }


  return (
    <div>
      {!editMode &&
        <div>
          <span className={style.statusClick} onClick={activateEditMode}>{props.status || "_____"}</span>
        </div>
      }
      {editMode &&
        <div>
          <input className={style.input_status} onChange={onStatusChange} autoFocus={true} onKeyUp={keyUpUse} value={status} onBlur={deactivateEditMode}/>
        </div>
      }
    </div>
  )
}

export default ProfileStatusWithHooks;