import React from 'react';
import s from './Post.module.css';
import ava_post from '../../../../assets/images/post_ava.jpg'

const Post = (props) => {
  return (
    <div className={s.post}>
      <div className={s.new_post}>
        <img src={ava_post} alt=""/>
        <div className={s.post__text}>{props.message}</div>
        <div className={s.like}>Like  <span>{props.like}</span></div>
      </div>
    </div>
  )
};

export default Post;